.categories__list {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
  }

  @media (min-width: $desktop-width) {
    width: 730px;

    @include ul-default;
  }
}

.categories-list__item {
  position: relative;

  display: flex;
  flex-direction: column;
  margin-bottom: 25px;
  padding: 20px;

  background-color: $soft-gray;

  @media (min-width: $tablet-width) {
    width: 48.5%;
    min-height: 200px;
  }

  @media (min-width: $desktop-width) {
    width: 350px;
    min-height: 175px;
  }
}

.categories-list__item:nth-child(2n + 1) {
  @media (min-width: $tablet-width) {
    margin-right: 3%;
  }
}

.categories-list__item:nth-child(n + 5) {
  @media (max-width: $desktop-width-only) {
    display: none;
  }
}

.categories-list__title {
  order: 2;
  width: 130px;
  margin: 0 0 14px auto;

  font-weight: bold;
  font-size: 22px;
  line-height: 21px;
  font-family: $font-stack-o;
  text-align: right;
  color: $main-color;

  @media (min-width: $tablet-width) {
    order: 1;
    margin: 0 auto 14px 0;

    text-align: left;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
  }
}

.categories-list__subtitle {
  order: 3;
  width: 130px;
  margin: 0 0 20px auto;

  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  font-family: $font-stack-o;
  text-align: right;
  color: $soft-black;

  @media (min-width: $tablet-width) {
    order: 2;
    width: 170px;
    margin: 0 auto 20px 0;

    text-align: left;
  }

  @media (min-width: $desktop-width) {
    width: 180px;
  }
}

.categories-list__image {
  position: absolute;
  bottom: 97px;

  width: 110px;

  @media (max-width: $mobile-width-only) {
    left: 20px;
  }

  @media (min-width: $tablet-width) {
    right: 20px;

    width: 110px;
  }
}

.categories-list__hr {
  position: relative;
  top: 0;

  order: 4;
  width: 100%;
  height: 2px;
  margin: 0;
  margin-top: auto;

  background-color: $border-gray;
  border: 0;

  &::after {
    content: "";
    position: absolute;
    top: 1px;

    width: 100%;
    height: 1px;

    background-color: $white;
  }
}

.categories-list__button {
  position: relative;

  display: block;
  order: 5;
  width: auto;
  margin: 20px 0 0;
  padding: 5px 0;

  &::after {
    content: "";
    position: absolute;
    top: 13px;

    width: 12px;
    height: 7px;
    margin-left: 10px;

    background: url("../img/right-arrow.svg");
    background-repeat: no-repeat;
    background-size: 100%;
  }
}
