.social__list {
  display: flex;
  justify-content: start;

  @include ul-default($margin: 20px 0);

  @media (min-width: $tablet-width) {
    margin-top: 34px;
  }
}

.social__link {
  &--instagram {
    margin: 0 20px;
  }

  &--youtube {
    width: 34px;
  }
}

.social__link svg {
  fill: $white;

  @media (min-width: $tablet-width) {
    fill: $soft-black;
  }

  @media (min-width: $desktop-width) {
    fill: $white;
  }
}
