body {
  min-width: 280px;
  margin: 0;
  padding: 0;

  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  font-family: $font-stack-r;
  color: $soft-black;

  background-color: $white;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}
