.contacts {
  @media (min-width: $tablet-width) {
    position: relative;
  }
}

.contacts__map {
  display: none;
  height: 480px;

  @media (min-width: $tablet-width) {
    display: block;
  }
}

.contacts__title {
  margin: 20px;

  font-weight: normal;
  font-size: 20px;
  line-height: 30px;
  font-family: $font-stack-o;
  text-align: center;
  color: $soft-black;
  text-transform: uppercase;

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.contacts__form-wrap {
  @media (min-width: $desktop-width) {
    position: relative;

    width: 1200px;
    margin: 0 auto;
  }
}

.contacts__form {
  margin: 0 0 20px 0;
}
